import React from "react";
import Canvas from "./Canvas";
import ArtChoiser from "./ArtChoiser";
import PollockConf from "./PollockConf";
import CirclesConf from "./CirclesConf";
import SquaresConf from "./SquaresConf";
import RowsConf from "./RowsConf";

const defaultOptions = {
  activeArts: {
    pollock: 1,
    circles: false,
    squares: false,
    rows: false,
  },
  pollockConf: {
    count: 5,
    lineWidth: 5,
    fill: true,
  },
  circlesConf: {
    count: 5,
    radiusC: 5,
    lineWidth: 5,
    fill: true,
  },
  squaresConf: {
    count: 5,
    sizeC: 5,
    lineWidth: 5,
    fill: true,
  },
  rowsConf: {
    widthC: 5,
    heightC: 5,
    nice: false,
    lineWidth: 5,
    fill: true,
  },
};

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = defaultOptions;
    this.handleSelectArt = this.handleSelectArt.bind(this);
  }

  handleSelectArt = (type, value) => {
    const newState = {
      activeArts: {
        ...this.state.activeArts,
        [type]: value,
      },
    };
    this.setState(newState);
  };

  handlePollockConfChange = (type, value) => {
    const newState = {
      pollockConf: {
        ...this.state.pollockConf,
        [type]: value,
      },
    };
    this.setState(newState);
  };

  handleCirclesConfChange = (type, value) => {
    const newState = {
      circlesConf: {
        ...this.state.circlesConf,
        [type]: value,
      },
    };
    this.setState(newState);
  };

  handleSquaresConfChange = (type, value) => {
    const newState = {
      squaresConf: {
        ...this.state.squaresConf,
        [type]: value,
      },
    };
    this.setState(newState);
  };

  handleRowsConfChange = (type, value) => {
    const newState = {
      rowsConf: {
        ...this.state.rowsConf,
        [type]: value,
      },
    };
    this.setState(newState);
  };

  render() {
    return (
      <div style={{ display: "flex" }}>
        <div style={{ width: "25%" }}>
          <ArtChoiser
            activeArts={this.state.activeArts}
            onSelect={this.handleSelectArt}
          />
          {this.state.activeArts.pollock && (
            <PollockConf
              onChange={this.handlePollockConfChange}
              {...this.state.pollockConf}
            />
          )}
          {this.state.activeArts.circles && (
            <CirclesConf
              onChange={this.handleCirclesConfChange}
              {...this.state.circlesConf}
            />
          )}
          {this.state.activeArts.squares && (
            <SquaresConf
              onChange={this.handleSquaresConfChange}
              {...this.state.squaresConf}
            />
          )}
          {this.state.activeArts.rows && (
            <RowsConf
              onChange={this.handleRowsConfChange}
              {...this.state.rowsConf}
            />
          )}
        </div>
        <Canvas
          pollockConf={this.state.pollockConf}
          circlesConf={this.state.circlesConf}
          squaresConf={this.state.squaresConf}
          rowsConf={this.state.rowsConf}
          activeArts={this.state.activeArts}
        />
      </div>
    );
  }
}

export default App;



